*/
  li{
    display: inline-block;
    color: black;
    
  }
  li a{
    padding: 14px 10px;
    text-decoration: none;
    font-size: 1.5em;
    color: rgb(105, 104, 104)
  }
  li a:hover{
    background-color: #4CAF50;
  }
  ul {
    border: 1px solid #e7e7e7;
    background-color: #f3f3f3;
    padding: 0;
    margin: 0;
    position: fixed;
    top: 0;
    left:0;
    width: 100%;
    list-style-type: none;
    overflow: hidden;
    text-align: left;
  }

  .MyInfo{
    display: flex;
    background-color:rgba(255, 255, 255, 0.836);
    margin: 29px auto 10px;
  }
  p{
    font-size: small;
  }

  .Welcome{
    flex: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: 'row';
    padding: 10px;
  }
  .ContainPic{
    flex: 30%;
  }
  img{
    width:100%;
    height:100%;
  }

  .MyProjects{
    margin: 40px auto 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    justify-content: flex-start;
    
  }

  .project-box{
    flex-grow: 1;
    min-height: 300px;
    flex-basis: 30%;
    margin: 10px;
    border: black solid 1px;
    background-color:rgba(255, 255, 255, 1);
  }

  .elevator-pitch{
    font-size: 20px;
    padding: 20px;
  }


  a{
    padding-right: 10px;
  }

  .main-margins{
    margin:  20px 80px 0px;
  }
  @media only screen and (min-device-width: 320px)
and (max-device-width: 480px)
and (-webkit-min-device-pixel-ratio: 2){
  .MyInfo{
    display:flex;
    flex-direction: column;
    margin-top: 29px;
  }
  .main-margins{
    margin: 20px auto 0px;
  }
  .MyProjects{
    display:flex;
    flex-direction: column;
  }
  
}

  
  
  
  
  
  